<template>
	<div class="purchase-function">
		<div class="function-box">
			<div>
				<p class="profit-title">采购量</p>
				<p class="profit-value">{{allOrderSize}}</p>
			</div>
			<div>
				<p class="profit-title">采购笔数</p>
				<p class="profit-value">{{allOrderNum}}..</p>
			</div>
			<div>
				<p class="profit-title">采购金额</p>
				<p class="profit-value">￥{{zongJIage}}</p>
			</div>
		</div>
		<div class="function-box">
			<div v-for="(item,index) in profitArrTitle" :key="index" class="profit-btns" @click="profitClick(index)">
				{{item}}
			</div>
		</div>
	</div>
</template>

<script>



export default {
  name: 'purchaseFunctionNav',
  data(){
	return{
		profitArrTitle:[
			'商品名称',
			'数量',
			'折后金额',
			'其他',
		],
		zongJIage:0
	}
  },
  methods:{
	profitClick(index){
		this.$emit("profitClick",index)
	},
	//计算总属性
	MatchAllAttr(arr,key){
		if(arr.length<=0) return 0
		let res=0
		arr.forEach(obj=>{
			res+=parseInt(obj[key])
		})
		return res
	},
	//总金额
	allMoney(arr){
		if(arr.length<=0) return 0
		let res=0
		arr.forEach(obj=>{
			res+=obj['productnum']*obj['price']
		})
		return res
	}
  },
  props:{
	profitArrs:{
		type:Array,
		default(){
			return []
		}
	}
  },
  computed:{
	allOrderMoney(){
		let res=0
		
		return res
	},
	//订单数量量
	allOrderNum(){
		return this.profitArrs.length
	},
	//总价格
	allOrderSize(){
		if(this.profitArrs.length<=0) return 0
		let res=0
		this.profitArrs.forEach(obj=>{
			res+=parseInt(obj.productnum)
		})
		return res
	},
	//采购金额
	caigouMoney(){
		let res=0
		return res
	},
	profit(){
		return this.profitArrs
	}
  },
  //对象可以不监听但是数组需要watch
  watch:{
	profit(newVal){
		this.zongJIage=this.allMoney(newVal).toFixed(2)
	}
  }
}
</script >

